
import { h } from "preact";
import { CSSProperties } from "preact/compat";
import LoadingImage from "../../general/loadingImage";

interface Skill {
  name: string,
  officialSiteURL: string,
  logo: string
}

interface DisplaySkillsProps {
  skills: Skill[]
  style?: CSSProperties
}

function DisplaySkills(props: DisplaySkillsProps) {
  return (
    <div style={props.style}>

      {props.skills.map(elem => {
        return (
          <LoadingImage
            a={{
              href: elem.officialSiteURL
            }}
            img={{
              style: { height: '3rem', width: '3rem' },
              src: elem.logo,
              alt: elem.name
            }}
          />
          // <a href={elem.officialSiteURL}>
          //   <img style={{ height: '3rem', width: '3rem' }} src={elem.logo} alt={elem.name} />
          // </a>
        );
      })}
          
    </div>
  );
};

export { DisplaySkills, Skill };