
import { h } from "preact";
import LineGraphic from "../resume_section_subcomponents/lineGraphic";
import SubComponentInfo from "../resume_section_subcomponents/subComponentInfo";
import LoadingImage from "../../../general/loadingImage";
import RoleType from "./roleType";
import SubComponentDescript from "../resume_section_subcomponents/subComponentDescript";
import SubComponentPoints from "../resume_section_subcomponents/subComponentPoints";


interface RoleDesktopProps {
  role: RoleType
}
function RoleDesktop(props: RoleDesktopProps | any) {
  const role: RoleType = props.role;

  return (
    <div className={"subsectionRow"}>

      {/* media */}

      <div style={{ display: 'flex', width: '48%', justifyContent: 'right'}}>
        <LoadingImage className={"center"}
          style={{ width: '100%', position: 'relative' }}
          img={{
            src: role.media[0],
            alt: "na",
            style: {
              objectFit: 'contain',
              position: 'absolute',
              top: '0',
              left: '0',
              width: '100%',
              height: '100%'
            }
          }}
        />
      </div>

      {/* decorative divider */}
      <LineGraphic style={{ display: 'flex', justifyContent: 'center', width: '4%' }} color="black" width="1px"/>

      {/* role information */}
      <div style={{ display: 'flex', width: '48%', justifyContent: 'left'}}>
        <SubComponentInfo title={role.title}
          startDate={new Date(role.startDate)} endDate={role.endDate ? new Date(role.endDate) : null} current={role.current}
        >
          <SubComponentDescript description={role.description} showTitle={true}/>
          <SubComponentPoints points={role.points} showTitle={true} />
        </SubComponentInfo>
      </div>
    </div>
  );
  
};

export default RoleDesktop;