import { h } from "preact";
import RoleType from "./responsiveRolesComponents/roleType";
import RoleDesktop from "./responsiveRolesComponents/roleDesktop";
import RoleMobile from "./responsiveRolesComponents/roleMobile";


interface RoleProps {
  role: RoleType
}
function Role(props: RoleProps | any) {
  const role: RoleType = props.role;

  const mql = window.matchMedia('(min-width: 700px)');

  if (mql.matches) {
    return <RoleDesktop role={role}/>;
  }
  else {
    return <RoleMobile role={role}/>;
  }
  
};

export { Role, RoleType };