import { h } from "preact";
import { CSSProperties } from "preact/compat";

interface LineGraphicProps {
  style: CSSProperties | undefined,
  headRadiusVW?: number, 
  color?: string | undefined,
  width?: string | undefined
}

function LineGraphic(props: LineGraphicProps) {
  return (
    <div style={props.style}>
      <div style={{ display: 'flex', flexDirection: 'column', marginTop: '.25rem', marginBottom: "1rem" }}>
        <div style={{ display: 'flex' }}>
          <div style={{
              borderRadius: '50%',
              width: props.headRadiusVW ? `${props.headRadiusVW}vw` : '1.25rem',
              height: props.headRadiusVW ? `${props.headRadiusVW}vw` : '1.25rem',
              border: `${props.width ?? '2px'} solid ${props.color ?? 'black'}`
            }}>{/* circle */}
          </div>
        </div>
        <div style={{ display: 'flex', justifyContent: 'center', height: '100%' }}>
          <div style={{ 
            background: props.color ?? 'black',
            width: props.width ?? '2px',
            height: '100%',
          }}>{/* line */}</div>
        </div>
      </div>
    </div>
  );
};

export default LineGraphic;