import { Button, Menu, MenuItem } from "@mui/material";
import { h } from "preact";
import { useState } from "preact/hooks";

enum MenuPage {
  "resume",
  "art",
  "about"
}

interface HeaderProps {
}

function WebsiteHeader() {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  const open = Boolean(anchorEl);

  function handleButtonClick(event: any) {
    setAnchorEl(event.currentTarget);
  };
  function handleButtonClose() {
    setAnchorEl(null);
  };
  function selectOption(page: MenuPage) {
    // redirect
    switch (page) {
      case MenuPage.about:
        window.location.href = '/';
        break;
      case MenuPage.resume:
        window.location.href = '/resume';
        break;
      case MenuPage.art:
        window.location.href = '/art';
        break;
    }
    setAnchorEl(null);
  };
  
  return (
    <header>
      <div>
        <Button
          id="basic-button"
          aria-controls={open ? 'basic-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          onClick={handleButtonClick}
        >
          Menu
        </Button>
        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleButtonClose}
          MenuListProps={{
            'aria-labelledby': 'basic-button',
          }}
        >
          <MenuItem onClick={() => selectOption(MenuPage.about)}>About</MenuItem>
          <MenuItem onClick={() => selectOption(MenuPage.resume)}>Resume</MenuItem>
          <MenuItem onClick={() => selectOption(MenuPage.art)}>Art</MenuItem>
        </Menu>
      </div>
      <h1>{process.env.WEBSITE_TITLE}</h1>
    </header>
  );
};

export { WebsiteHeader, MenuPage };