import { h } from "preact";
import LineGraphic from "../resume_section_subcomponents/lineGraphic";
import LoadingImage from "../../../general/loadingImage";
import CardCarousel from "../../../general/cardCarousel";
import SubComponentDescript from "../resume_section_subcomponents/subComponentDescript";
import SubComponentPoints from "../resume_section_subcomponents/subComponentPoints";
import RoleType from "./roleType";
import SubComponentInfo from "../resume_section_subcomponents/subComponentInfo";


interface RoleMobileProps {
  role: RoleType
}
function RoleMobile(props: RoleMobileProps | any) {
  const role: RoleType = props.role;

  const mql = window.matchMedia('(min-width: 700px)');

  return (
    <div className={"subsectionRow"}>
      <LineGraphic style={{ display: 'flex', justifyContent: 'center', width: '6%' }} color="black" width="1px"/>
      <div style={{ display: 'flex', width: '94%', justifyContent: 'left'}}>
        <SubComponentInfo 
          title={role.title}
          startDate={new Date(role.startDate)} endDate={role.endDate ? new Date(role.endDate) : null} current={role.current}
          childMarginLeft="0"
        >
          <CardCarousel
            className={"subsectionCarousel"}
            style={{
              width: "91%"
            }}
            cardAttr={{
              style: {
                width: "95%"
              }
            }}
            centerPerc={0}
          >
            <div className={"subsectionCard"}>
              <SubComponentPoints points={role.points} showTitle={role.description !== undefined} />
            </div>

            <div className={"subsectionCard"}>
              <SubComponentDescript description={role.description} showTitle={role.points !== undefined && role.points.length > 0}/>
            </div>

            <div className={"subsectionCard center"}>
              <LoadingImage className={"center"}
                style={{ width: '100%', position: 'relative' }}
                img={{
                  src: role.media[0],
                  alt: "na",
                  style: {
                    objectFit: 'contain',
                    position: 'absolute',
                    top: '0',
                    left: '0',
                    width: '100%',
                    height: '100%'
                  }
                }}
              />
            </div>
          </CardCarousel>
        </SubComponentInfo>
      </div>
    </div>
  );
  
};

export default RoleMobile;