
import { h } from "preact";
import { CSSProperties } from "preact/compat";
import LineGraphic from "./resume_section_subcomponents/lineGraphic";
import SubComponentInfo from "./resume_section_subcomponents/subComponentInfo";
import SubComponentDescript from "./resume_section_subcomponents/subComponentDescript";
import SubComponentPoints from "./resume_section_subcomponents/subComponentPoints";

interface ProjectType {
  name: string,
  logo: string,
  description: string,
  points: string[],
  field: string,
  current: boolean,
  startDate: string,
  endDate: string
}

interface ProjectProps {
  proj: ProjectType,
  style?: CSSProperties
}

function Project(props: ProjectProps) {
  const proj = props.proj;
  return (
    <div className={"stack"}>
      <div className={"subsectionRow"}>
        
        <LineGraphic style={{ display: 'flex', justifyContent: 'center', width: '4%' }} color="black" width="1px"/>

        <div style={{ display: 'flex', width: '96%', justifyContent: 'left'}}>
          <SubComponentInfo title={proj.name} startDate={new Date(proj.startDate)} endDate={proj.endDate ? new Date(proj.endDate) : null} current={proj.current}
          >
            <SubComponentDescript description={proj.description} showTitle={true}/>
            <SubComponentPoints points={proj.points} showTitle={true} />
          </SubComponentInfo>
        </div>

      </div>
    </div>
  );
};

export { Project, ProjectType };