import { h } from "preact";
import { CSSProperties } from "preact/compat";

interface SubComponentDescriptProps {
  description: string
  showTitle: boolean
  style?: CSSProperties
}
function SubComponentDescript(props: SubComponentDescriptProps) {
  return (
    <div style={props.style}>
      {props.showTitle &&
        <h5>Description:</h5>
      }
      <p className={ props.showTitle ? 'titledSubCompInner' : ''}>{props.description}</p>
    </div>
  );
};

export default SubComponentDescript;