
import { h } from "preact";
import { Variant } from "@mui/material/styles/createTypography";
import { SxProps, Theme } from "@mui/material";

interface DateRangeProps {
  startDate: Date,
  endDate: Date | null,
  current?: boolean
  style?: SxProps<Theme>,
  variant?: Variant
}

function DateRange(props: DateRangeProps) {
  let range = `${props.startDate.toLocaleString('default', {
    year: "numeric",
    month: "long"
  })} - `;

  if (props.current) {
    range += `Present`;
    if (props.endDate) {
      range += ` (expected end date: ${props.endDate.toLocaleString('default', {
        year: "numeric",
        month: "long"
      })})`
    }
  }
  else {
    range += `${props.endDate?.toLocaleString('default', {
      year: "numeric",
      month: "long"
    })}`
  }

  return (
    <p className="typography-body2" style={{ fontStyle: 'italic' }}>
      {range}
    </p>
  );
};

export default DateRange;