
import { h } from "preact";
import SubSectionTitle from "./subSectionTitle";
import { CSSProperties } from "preact/compat";
import SubComponentInfo from "./resume_section_subcomponents/subComponentInfo";
import LineGraphic from "./resume_section_subcomponents/lineGraphic";
import SubComponentDescript from "./resume_section_subcomponents/subComponentDescript";

interface CredentialType {
  title: string,
  field: string,
  description: string,
  current: boolean,
  startDate: string,
  endDate: string
}

interface Institution {
  name: string,
  logo: string,
  credentials: CredentialType[]
}

interface EducationProps {
  inst: Institution,
  style?: CSSProperties
}

function Education(props: EducationProps) {
  const inst = props.inst;
  return (
    <div className={"stack"}>
      <div>

        <SubSectionTitle name={inst.name} logo={inst.logo} />

        <div style={{ display: 'flex', flexDirection: 'column' }}>
          {inst.credentials.map(cred => 
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'center',
              width: '100%', 
              marginTop: '1rem'
            }}>
              <LineGraphic 
                style={{ 
                  display: 'flex', 
                  justifyContent: 'center', 
                  width: '4%' 
                }} 
                color="black" 
                width="1px"
              />
              <div style={{ display: 'flex', width: '96%', justifyContent: 'left'}}>
                <SubComponentInfo 
                  title={cred.title} 
                  startDate={new Date(cred.startDate)}
                  endDate={cred.endDate ? new Date(cred.endDate) : null}
                  current={cred.current}
                >
                  <SubComponentDescript description={cred.description} showTitle={false}/>
                </SubComponentInfo>
              </div>
            </div>
          )}
        </div>

      </div>
    </div>
  );
};

export { Education, Institution };