import { h } from "preact";

interface SubComponentPointsProps {
  points: string[]
  showTitle: boolean
}

function SubComponentPoints(props: SubComponentPointsProps) {
  return (
    <div>
      {props.showTitle &&
        <h5>Key points:</h5>
      }
      <ul className={ props.showTitle ? 'titledSubCompInner' : ''}>
        {props.points.map((point: string) => 
          <li><p>{point}</p></li>
        )}
      </ul>
    </div>
  );
};

export default SubComponentPoints;