
import { h } from "preact";
import {Role, RoleType} from "./role";
import SubSectionTitle from "./subSectionTitle";

interface Organization {
  name: string;
  logo: string;
  location: string;
  description: string;
  roles: RoleType[];
}

interface ExperienceProps {
  org: Organization
}

function Experience(props: ExperienceProps | any) {
  const org = props.org;
  return (
    <div className={"center"}><div>

      <SubSectionTitle name={org.name} logo={org.logo} />
      
      {/* description if applicable */}
      {org.description &&
        <p style={{ marginTop: "1rem" }}>{org.description}</p>
      }

      {/* corresponding roles */}
      <div className={'stack'}>
        {org.roles.map((role: any) => <Role role={role} />)}
      </div>

    </div></div>
  );
};

export { Experience, Organization };