
import { h } from "preact";
import { CSSProperties, useState } from "preact/compat";
import { JSXInternal } from "preact/src/jsx";

interface LoadingImageProps extends JSXInternal.HTMLAttributes<HTMLDivElement> {
  img: JSXInternal.HTMLAttributes<HTMLImageElement>,
  a?: JSXInternal.HTMLAttributes<HTMLAnchorElement>
}

function LoadingImage(props: LoadingImageProps) {

  let [loading, setLoading] = useState(true);

  const {img, a, ...attr} = props;

  return (
    <div
      {...attr}
      className={loading ? "loadingImg " + props.className : props.className}
    >
      {a ?
        <a {...a}>
          <img {...img} onLoad={() => setLoading(false)} />
        </a>
        :
        <img {...img} onLoad={() => setLoading(false)} />
      }
    </div>
  );
};

export default LoadingImage;