
import { h } from "preact";
import { useState } from "preact/hooks";

interface CardCarouselProps extends h.JSX.HTMLAttributes<HTMLDivElement> {
  children: h.JSX.Element[],
  cardAttr?: h.JSX.HTMLAttributes<HTMLDivElement>,
  dragThreshold?: number,
  centerPerc?: number
}
function CardCarousel(props: CardCarouselProps) {
  const {cardAttr, children, dragThreshold, className, ...attr} = props;

  const [index, setIndex] = useState<number>(0);

  function handleInteraction(e: h.JSX.TargetedMouseEvent<HTMLDivElement> | h.JSX.TargetedTouchEvent<HTMLDivElement>) {

    const isTouch = (e.type === "touchstart");
    let card = e.target as any;
    let offset = 0;
    let initialX = isTouch ? 
      (e as h.JSX.TargetedTouchEvent<HTMLDivElement>).touches[0].clientX : 
      (e as h.JSX.TargetedMouseEvent<HTMLDivElement>).clientX;
    
    function onCursorMove(e: any) {
      offset = (isTouch ? e.touches[0].clientX : e.clientX) - initialX;
      card.style.left = offset + "px";
    }

    function onCursorStop(e: any) {
      document.onmousemove = null;
      document.onmouseup = null;

      // drag left
      if (offset < -(dragThreshold ?? 60)) {
        if (index + 1 < children.length) {
          setIndex(index + 1);
        }
      }
      // drag right
      else if (offset > (dragThreshold ?? 60)) {
        if (index - 1 >= 0) {
          setIndex(index - 1);
        }
      }

      card.style.left = null;
    }

    if (isTouch) {
      document.ontouchmove = onCursorMove;
      document.ontouchend = onCursorStop;
    }
    else {
      document.onmousemove = onCursorMove;
      document.onmouseup = onCursorStop;
    }
  }

  return (
    <div {...attr} className={className ?? ""}>
      <div {...attr} className={`cardCarousel ${className ?? ""}`}>
        {children.map((elem, n) => {
          
          let position = "";
          if (n < index) {
            position = "prevCard";
          }
          else if (n === index) {
            position = "activeCard";
          }
          else if (n === index + 1) {
            position = "nextCard";
          }

          return (
            <CarouselCard 
              {...cardAttr}
              className={`${position} ${cardAttr?.className ?? ""}`}
              onCursorDown={handleInteraction}
              centerPerc={props.centerPerc}
            >
              {elem}
            </CarouselCard>
          );
        })}
      </div>
    </div>
  )
}

interface CarouselCardProps extends h.JSX.HTMLAttributes<HTMLDivElement> {
  children: h.JSX.Element,
  className: string,
  onCursorDown: (e: h.JSX.TargetedMouseEvent<HTMLDivElement> | h.JSX.TargetedTouchEvent<HTMLDivElement>) => void,
  centerPerc?: number
}
function CarouselCard(props: CarouselCardProps) {
  const {children, onCursorDown, style, className, ...attr} = props;

  return (
    <div 
      style={{
        left: props.centerPerc !== undefined ? `${props.centerPerc}%` : undefined,
      }}
      className={"carouselCardCover"}
    >
      <div 
        {...attr} 
        className={`carouselCard ${className ?? ""}`}
        onMouseDown={onCursorDown}
        onTouchStart={onCursorDown}
        style={{ ...(style as h.JSX.CSSProperties) }}
      >
        <div style={{ pointerEvents: "none", width: "100%", height: "100%" }}>
          {children}
        </div>
      </div>
    </div>
  )
}

export default CardCarousel;