
import { h } from "preact";
import SubComponentDescript from "./subComponentDescript";
import SubComponentPoints from "./subComponentPoints";
import DateRange from "../dateRange";

interface SubComponentInfoProps {
  children: h.JSX.Element[] | h.JSX.Element,
  title: string,
  startDate: Date,
  endDate: Date | null,
  current: boolean,
  childMarginLeft?: string
}

function SubComponentInfo(props: SubComponentInfoProps) {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', marginLeft: '1rem'}}>
      <h4>{props.title}</h4>
      <DateRange startDate={props.startDate} endDate={props.endDate} current={props.current} />
      <div style={{ display: 'flex', flexDirection: 'column', marginLeft: props.childMarginLeft ?? "2rem" }}>
        {props.children}
      </div>
    </div>
  );
};

export default SubComponentInfo;